
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'Tjjy',
components: {
XHeader
},
data(){
return{
model:{
password:'',
realpassword:''
},
schema:{
fields:[

{
type:'input',
modelKey:'password',
label:'新密码',
props:{
placeholder:'请输入新密码',
type:'password',
eye:{
open:false,
}
},
rules:{
required:true,
},
trigger:'blur',
},
{
type:'input',
modelKey:' realpassword',
label:'确认密码',
props:{
placeholder:'请再次输入新密码',
type:'password',
eye:{
open:false,
}
},
rules:{
required:true,
},
trigger:'blur',
},
{
type:'submit',
label:'提交'
}
]
}
}
},
methods:{

submitHandler(e){
e.preventDefault()
this.$dialog.alert({mes: '密码重置成功'});
this.$router.push({ path: '/' })

}
}

}



