import MescrollVue from 'mescroll.js/mescroll.vue'
export default {
  inject: ['reload'],
  name: 'CaseList',
  components: {
       MescrollVue
  },
  data () {
          return {
            dataList: []

		  }

  },
   mounted() {
      this.a()
    },
  methods: {
   a(){
         var token=window.localStorage.getItem("access_token")

        this.$axios.get('/api/v1/visit',{params:{ 'access-token': token,page: 1}}).then(res=>{

              this.dataList = res.data

        })
      },
   onClick(a){
          var id = a
          var token=window.localStorage.getItem("access_token")
          this.$axios.delete("/api/v1/visit/"+id,{params:{ 'access-token': token}}).then(res=>{
            if(res.code=='200'){
                this.$dialog.alert({mes: '删除成功'});
                this.reload()
            }


        })
     }

  }



}



