  export default {
    name:'CaseList',
    data () {
      return {
        searchvaldata:[],
         offset:1
      }
    },
    mounted() {
      this.a()
    },
    methods:{
      a(){
         var token=window.localStorage.getItem("access_token")

        this.$axios.get('/api/v1/article',{params:{ 'access-token': token,cid: 3, page: 1}}).then(res=>{

              this.searchvaldata = res.data

        })
      },
	  refresh(done) {
        this.offset++
        var token=window.localStorage.getItem("access_token")
       this.$axios.get('/api/v1/article',{params:{ 'access-token': token,cid: 3, page: 1}}).then(res=>{
          if(res.data.length!=0){
            this.searchvaldata=this.searchvaldata.concat(res.data)
            done()
          }else{
            done(true)
          }
        })
      },
      infinite(done) {
        this.offset++
        var token=window.localStorage.getItem("access_token")
       this.$axios.get('/api/v1/article',{params:{ 'access-token': token,cid: 3, page: 1}}).then(res=>{
          if(res.data.length!=0){
            this.searchvaldata=this.searchvaldata.concat(res.data)
            done()
          }else{
            done(true)
          }
        })
      }

    }
  }




