
import XHeader from 'vux/src/components/x-header/index.vue'

import Vue from 'vue';
import {TextArea} from 'vue-ydui/dist/lib.rem/textarea';


Vue.component(TextArea.name, TextArea);

export default {
name: 'Tjjy',
components: {
XHeader
},
data(){
return{

model:{

email:'',
phone: ''
},
schema:{
fields:[

{
type:'input',
modelKey:'phone',
label:'手机号码',
props:{
placeholder:'请输入手机号码',
type:'number',

},
rules:{
required:true,
type:'number',
},
trigger:'blur',
messages:{
required:'手机号码不能为空',
type:'手机号码必须为数字'

}
},
{
type:'input',
modelKey:'email',
label:'邮箱',
props:{
placeholder:'请输入邮箱',

},
rules:{
required:true,
type:'string',
},
trigger:'blur',
messages:{
required:'邮箱不能为空',

}
},
{
type:'submit',
label:'提交'
}
]
}
}
},
methods:{
submitHandler(e){
e.preventDefault()
this.$dialog.alert({mes: '提交失败'});


}
}


}



