
import Vue from 'vue';
import {Layout} from 'vue-ydui/dist/lib.rem/layout';
Vue.component(Layout.name, Layout);
import MescrollVue from 'mescroll.js/mescroll.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'JFFrom',
components: {
XHeader,
MescrollVue
},
data() {
return {
validity: {},
valid: undefined,
model: {
identity: '',
name: '',
number: '',
phone: '',
checkboxGroupValue:[],
uploadValue: []
},
schema:{
fields:[

{
type:'input',
modelKey:'identity',
label:'身份证号',
props:{
placeholder:'请输入身份证号,没有请填写 无',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:'身份证号不能为空'

}
},
{
type:'input',
modelKey:'name',
label:'姓名',
props:{
placeholder:'请输入姓名',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:'姓名不能为空'

}
},
{
type:'input',
modelKey:'number',
label:'车牌号码',
props:{
placeholder:'请输入车牌号码',
},
rules:{

required:true,
type:'string'

},
trigger: 'blur',
messages:{
required:'车牌号码不能为空'

}
},
{
type:'input',
modelKey:'phone',
label:'手机号',
props:{
placeholder:'请输入手机号',
},
rules:{

required:true,
type:'number'

},
trigger: 'blur',
messages:{
required:'手机号不能为空'

}
},
{
type: 'checkbox-group',
modelKey: 'checkboxGroupValue',
label: '家访性质',
props: {
options: ['交通事故典型案例通报', '尊法守法提醒', '询问有无涉法诉求','征求交管工作建议','工作满意度调查']
},
rules: {
required: true
},
trigger: 'blur',
messages:{
required:'家访性质不能为空'

}
},
{
type: 'upload',
modelKey: 'uploadValue',
label: '上传图片',
events: {
'file-removed': (...args) => {
console.log('file removed', args)
}
}


},
{
type:'submit',
label:'提交'
},
{
type:'reset',
label:'重置'
}
]
}

}
},
methods: {
submitHandler(e) {
e.preventDefault()
try{
var a,b,c,d
a = this.model.checkboxGroupValue
b= a.join(";")
c= this.model.uploadValue
d= c.join("|")
console.log(b)
console.log(d)
var token=window.localStorage.getItem("access_token")
this.$axios({method: 'post',url: '/api/v1/visit?access-token='+token,data:
{identity: this.model.identity,name: this.model.name,
number: this.model.number,phone: this.model.phone,
content: b,org_id: 1,pics: d,}}).then((res)=>{
if(res.code=='200'){
this.$dialog.alert({mes: '提交成功'})

}else{
alert(res.data.message)
}
})

}catch(err){
console.log(err)
}

},
resetHandler(e) {
console.log('reset', e)
}
}
}



