
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: 'Grxx',
components: {
XHeader
},
data(){
return{

model:{
name: window.localStorage.getItem("realname"),
email: window.localStorage.getItem("email"),
phone: window.localStorage.getItem("mobile")
},
schema:{
fields:[

{
type:'input',
modelKey:'name',
label:'姓名',
props:{
placeholder:'请输入姓名',
},
rules:{

required:true,
type:'string',
min:2,

},
trigger: 'blur',
messages:{
required:'姓名不能为空',
min:'姓名不能少于2个字符',

}
},

{
type:'input',
modelKey:'phone',
label:'手机号码',
props:{
placeholder:'请输入手机号码',
type:'number',

},
rules:{
required:true,
type:'number',
},
trigger:'blur',
messages:{
required:'手机号码不能为空',
type:'手机号码必须为数字'

}
},
{
type:'input',
modelKey:'email',
label:'邮箱',
props:{
placeholder:'请输入邮箱',

},
rules:{
required:true,
type:'string',
},
trigger:'blur',
messages:{
required:'邮箱不能为空',

}
},
{
type:'submit',
label:'提交'
}
]
}
}
},
methods:{
submitHandler(e){
e.preventDefault()
try{
var id = 5186
var token=window.localStorage.getItem("access_token")
this.$axios({
method: 'put',
url:'/api//v1/member/5186?access-token=' +token,
data: {
realname: this.model.name,
mobile: this.model.phone,
email: this.model.email

}
}).then((res)=>{

if(res.code=='200'){
this.$dialog.alert({mes: '修改成功'});
}else{
this.$dialog.alert({mes: '修改失败'});
}
})

}catch(err){
console.log(err)
}

}
}

}



