import Vue from 'vue'
import Router from 'vue-router'
import Czmm from '@/pages/mine/czmm/Czmm'
import Grxx from '@/pages/mine/grxx/Grxx'
import Gywm from '@/pages/mine/gywm/Gywm'
import Sjtj from '@/pages/mine/sjtj/Sjtj'
import Tjjy from '@/pages/mine/tjjy/Tjjy'
import News from '@/pages/news/News'
import Case from '@/pages/case/Case'
import JiaFang from '@/pages/home/jiafang/JiaFang'
import JFFrom from '@/pages/home/jiafang/JFFrom'
import Jzh from '@/pages/home/jzh/Jzh'
import JzhFrom from '@/pages/home/jzh/JzhFrom'
import Jzq from '@/pages/home/jzq/Jzq'
import JzqFrom from '@/pages/home/jzq/JzqFrom'
import Kyaf from '@/pages/home/kyaf/Kyaf'
import AfFrom from '@/pages/home/kyaf/AfFrom'
import Mtjy from '@/pages/home/mtjy/Mtjy'
import MtFrom from '@/pages/home/mtjy/MtFrom'
import Xcdj from '@/pages/home/xcdj/Xcdj'
import DjFrom from '@/pages/home/xcdj/DjFrom'
import Login from '@/pages/login/Login'

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: '/Login',
    component: Login
  },
  {
    path: '/Home',
    name: '/Home',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/home/home/Home')
  },
  {
    path: '/Mine',
    name: '/Mine',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/mine/mine/Mine')
  },
  {
    path: '/News',
    name: '/News',
    component: News
  },
  {
    path: '/NewsContent/:id',
    name: '/NewsContent',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/news/components/NewsContent')
  },
  {
    path: '/Case',
    name: '/Case',
    component: Case
  },
  {
    path: '/CaseContent/:id',
    name: '/CaseContent',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/case/components/CaseContent')
  },
  {
    path: '/JiaFang',
    name: '/JiaFang',
    component: JiaFang
  },
  {
    path: '/JFFrom ',
    name: '/JFFrom ',
    component: JFFrom
  },
  {
    path: '/Jzh',
    name: '/Jzh',
    component: Jzh
  },
  {
    path: '/JzhFrom ',
    name: '/JzhFrom ',
    component: JzhFrom
  },
  {
    path: '/Jzq',
    name: '/Jzq',
    component: Jzq
  },
  {
    path: '/JzqFrom ',
    name: '/JzqFrom ',
    component: JzqFrom
  },
  {
    path: '/Aqxc',
    name: '/Aqxc',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/home/aqxc/Aqxc')
  },
  {
    path: '/XcFrom',
    name: '/XcFrom',
    component: () => import(/* webpackChunkName: "navigator" */ '@/pages/home/aqxc/XcFrom')
  },
  {
    path: '/Kyaf',
    name: '/Kyaf',
    component: Kyaf
  },
  {
    path: '/AfFrom',
    name: '/AfFrom',
    component: AfFrom
  },
  {
    path: '/Mtjy',
    name: '/Mtjy',
    component: Mtjy
  },
  {
    path: '/MtFrom',
    name: '/MtFrom',
    component: MtFrom
  },
  {
    path: '/Xcdj',
    name: '/Xcdj',
    component: Xcdj
  },
  {
    path: '/DjFrom',
    name: '/DjFrom',
    component: DjFrom
  },
  {
    path: '/Czmm',
    name: '/Czmm',
    component: Czmm
  },
  {
    path: '/Grxx',
    name: '/Grxx',
    component: Grxx
  },
  {
    path: '/Gywm',
    name: '/Gywm',
    component: Gywm
  },
  {
    path: '/Sjtj',
    name: '/Sjtj',
    component: Sjtj
  },
  {
    path: '/Tjjy',
    name: '/Tjjy',
    component: Tjjy
  }

]

export default new Router({
  mode: 'history',
  routes

})



